import { AuthService } from './auth.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from "@angular/router";

@Injectable()
export class AnonymousGuard implements CanActivate{

    constructor(
        private authService: AuthService,
        private router: Router
    ){}

    /*Az AuthGuard-ben tallhat canActivate fggvny ellentettje. Azt dnti el hogy az adott url-rl 
    elnaviglhatja-e magt a felhasznl a kvnt linkre. Ezt az alapjn teszi, hogy a felhasznl be van-e 
    jelentkezve. Ha be van jelentkezve, akkor a router elnaviglja a company url-re s false rtket ad vissza. 
    Ellenkez esetben true rtkkel tr vissza. Erre a guard-ra azrt van szksg, hogy a felhasznl 
    bejelentkezse utn ne tudja jra elrni a bejelentkezsi felletet, csak miutn kijelentkezett.*/
    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean{
        if(this.authService.isAuthenticated())
        {
            this.router.navigate(['company']);
            return false;
        }
        return true;
    }
}


