import { AuthService } from './auth.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from "@angular/router";

@Injectable()
export class AuthGuard implements CanActivate{

    constructor(
        private authService: AuthService,
        private router: Router
    ){}

    /*Azt dnti el hogy az adott url-rl elnaviglhatja-e magt a felhasznl a kvnt linkre. Ezt az alapjn 
    teszi, hogy a felhasznl be van-e jelentkezve. Ha nincs bejelentkezve, akkor a router elnaviglja a login 
    url-re s false rtket ad vissza. Ellenkez esetben true rtkkel tr vissza. Erre a guard-ra azrt van 
    szksg, hogy a felhasznl bejelentkezs nlkl ne tudja elrni az adatokat.*/
    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean{
        if(!this.authService.isAuthenticated())
        {
            this.router.navigate(['login']);
            return false;
        }
        return true;
    }
}


