import { ProjectService } from './../../projects/project.service';
import { CompanyService } from './../../companies/company.service';
import { Contact } from './../contact';
import { ContactService } from './../contact.service';
import { Component, OnInit, AfterViewChecked, ChangeDetectorRef } from '@angular/core';
import { BaseEditComponent } from '../../base/base-edit.component';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

//regulris kifejezsek:
const TEL_REGEX = /^\s*(?:\+?\d{1,3})?[- (]*\d{3}(?:[- )]*\d{3})?[- ]*\d{4}(?: *[x/#]\d+)?\s*$/;
const EMAIL_REGEX = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

@Component({
  selector: 'app-contact-edit',
  templateUrl: './contact-edit.component.html',
  styleUrls: ['./contact-edit.component.scss']
})
export class ContactEditComponent extends BaseEditComponent implements OnInit, AfterViewChecked {
	contactForm: FormGroup;
	contact: Contact;
	rank: number;
	companyChanged: boolean = false;
	projectChanged: boolean = false;

  constructor(
		public companyService: CompanyService,
		public projectService: ProjectService,
		protected route: ActivatedRoute,
		protected router: Router,
		public contactService: ContactService,
		private fb: FormBuilder,
		private changeDetector: ChangeDetectorRef
  ) {
    super(route, router);
  }

  	ngOnInit(): void{
		this.companyService.getItems();
		this.projectService.getItems();
		this.initform();
		if(this.contactService.getItems() && !this.contact){
			this.setThis();
		}
	}

	ngAfterViewChecked(): void{
		/*Mivel elbb tlt be a view, mint az adatok ezrt szksgvel az albbi metdusra, hogy ne kapjunk hibazenetet.*/
		if(!this.contact){
			this.setThis();
		} else {
			/*A router url-nek paramterei alapjn elszr is megvizsgljuk a num rtkt. Ez alapjn, ha 0 az 
			rtk akkor az array-ben cg id-k lesznek tallhatak, gy a CompanyService segtsgvel kinyerjk
			a megfelel cgeket s belerakjuk a nvjegy company mezjbe. Ha az rtk 1, akkor pedig az
			array-ben projektek lesznek tallhatk, melyek rtkeit a ProjectService nyeri ki neknk s gy el
			tudjuk trolni a nvjegy project mezjben.*/
			if(this.route.snapshot.params['num'] && this.route.snapshot.params['array']){
				if(this.contact.company.length === 0 && this.companyService.getItems()){
					switch (Number(this.route.snapshot.params['num'])) {
						case 0:
							this.route.snapshot.params['array'].split(",").forEach((x: number) =>{
								const company = this.companyService.getItem(+x);
								this.contact.company.push(company);
							});
							break;
						case 1:
							this.route.snapshot.params['array'].split(",").forEach((x: number) =>{
								const project = this.projectService.getItem(+x);
								this.contact.project.push(project)
							});
							break;
						default:
							break;
					}
				}
			}
		}
		this.changeDetector.detectChanges();
	}

	initform(): void{
		/*Ltrehozzunk egy formgroupot, amiben az input mezk s a rjuk vonatkoz jogosultsgi szablyok tallhatk.*/
		this.contactForm = this.fb.group({
			'contactCompany': [],
			'contactProject': [],
			'contactFullName': [null, Validators.required],
			'contactForename': [],
			'contactNickname': [],
			'contactSurename': [],
			'contactMiddlename': [],
			'contactPhone': [null, Validators.pattern(TEL_REGEX)],
			'contactEmail': [null, Validators.pattern(EMAIL_REGEX)],
		});
	}

	setThis(): void{
		if(this.route.snapshot.routeConfig.path == "new"){
			//Ha az url "new"-val egyenl, akkor teljesl
			this.setNew();
		} else {
			//Itt az url minden esetben "edit/:id" lesz s :id helyn, pedig a nvjegy id-je.
			this.setEdit();
		}
	}

	setNew(): void{
		this.contact = new Contact;
	}

	setEdit(): void{
		this.contact = Object.assign({}, this.contactService.getItem(+this.route.snapshot.params['id']));
		this.edit = true; //Ezen mez alapjn tudja a company-edit.component, hogy szerkeszteni kell vagy j cget ltrehozni
	}

	//Megvizsgl kt objektet, hogy azonosak-e.
	compareFn(c1: any, c2: any): boolean {
		return c1 && c2 ? c1.id === c2.id : c1 === c2;
	}

	add(contact: Contact): void{
		this.contactService.add(contact)
		if(contact.company.length > 0){
			this.companyService.modifyItems(contact);
		}
		if(contact.project.length > 0){
			this.projectService.modifyItems(contact);
		}
		this.navigateBack();
	}

	save(): void {
		this.contactService.update(this.contact);
		if(this.companyChanged){
			this.companyService.modifyItems(this.contact);
		}
		if(this.projectChanged){
			this.projectService.modifyItems(this.contact);
		}
		this.navigateBack();
	}

	//Submit lenyomsakor hvdik meg
	onSubmit(contact: Contact){
		if(this.contactForm.valid)  //Ha a validits megfelel
			this.edit? this.save() : this.add(contact);  //Ha az edit true, akkor a save hvdik meg, klnben az add
	}

}



