import { Contact } from './../contacts/contact';
import { Company } from './../companies/company';
import { Subject } from 'rxjs/Subject';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Project, Status, Priority, Currency } from './project';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { catchError, tap } from 'rxjs/operators';
import { BaseService } from '../base/base.service';
import 'rxjs/add/operator/map'
import { ProjectApiService } from './project.api.service';

@Injectable()
export class ProjectService extends BaseService{
    private projects: Project[]; //Loklisan is letroljuk az adatbzisbl kinyert adatokat a gyorsabb mkds miatt.
    private statuses: Status[];
    private priorities: Priority[];
    private currencies: Currency[];
    isLoading = true; //Az api-val kapcsolatos fggvnyek lefutsa ideig true rtket trol, majd ha vgzett a fggvny lefutott false-ra vltozik. Erre azrt van szksg, mert addig a felhasznlnak tltst jelenthetnk meg.
    isLoadingForEdit = true; //Ugyan az vonatkozik r, mint az isLoading-ra, csak ez az edit komponensben lesz hasznunkra.
    checkedArray = new Subject<number[]>();
    haveDone = new Subject<boolean>();

    constructor(
        private projectApiService: ProjectApiService
    ){
        super();
        this.projectApiService.getProjects().subscribe(
            (projects: Project[]) => {
                this.projects = projects;
                this.isLoading = false;
            }
        );
    }

	getItems(): Project[] {
		return this.projects;
    }

    /*Kinyerjk a loklisan trolt tbb-bl a neknk szksges projektet, vagy ha a tmb mg nem llna 
    rendelkezsre, akkor az api segtsgvel szerezzk meg az szksges adatokat. */
    getItem(project: Project | number): Project{
        const id = typeof project === 'number' ? project : project.id;
        if(this.projects)
            return this.projects.find((project: Project) => project.id === id);
        else{
            this.projectApiService.getProject(id).subscribe(
                (project: Project) => {
                    this.isLoading = false;
                    return project;
                }
            );
        }
    }

    getStatuses(): Status[] {
        return this.statuses;
    }

    getPriorities(): Priority[] {
        return this.priorities;
    }

    getCurrencies(): Currency[]{
        return this.currencies;
    }

    delete(project: Project | number): void{
        const id = typeof project === 'number' ? project : project.id;
        this.projects.splice(this.projects.indexOf(
            this.projects.find(deletedProject => deletedProject.id === id)), 1
        );
        this.projectApiService.deleteProject(id).subscribe();
    }

    add(project: Project): void{
        this.projectApiService.addProject(project).subscribe(
            (res: Response) => {
                project.id = res['project']['id'];
                console.log(project, res['project']['id']);
                this.projects.push(project);
            }
        );
    }

    update (project: Project): void{
        this.projects.find(oldProject => oldProject.id === project.id)[0] = project;
        this.projectApiService.updateProject(project).subscribe();
    }

    /*Edit komponens esetn szksgnk van az sszes prioritsra, sttuszra, pnznemre, ezrt ezeket beszerezzk 
    az api segtsgvel s ezt a metdust hvjuk meg szerkesztskor s j projekt felvitelekor. */
    getEditItems() {
        this.projectApiService.getStatuses().subscribe(
            (statuses: Status[]) => {
                this.statuses = statuses;
                this.projectApiService.getPriorities().subscribe(
                    (priorities: Priority[]) => {
                        this.priorities = priorities;
                        this.projectApiService.getCurrencies().subscribe(
                            (currencies: Currency[]) => {
                                this.currencies = currencies;
                                this.isLoadingForEdit = false;
                            }
                        );
                    }
                );
            }
        );
    }

    /* Hogy a felhasznli lmny a lehet legmegfelelbb legyen, s ha egy nvjegy mdostsa esetn ms 
    projekteket rendelnnk az adott nvjegyhez ne kelljen jra betlteni az alkalmazst, hogy a mr mdostott 
    projektet kapjuk meg ezrt van szksg erre a metdusra. Ez a metdos elszr is megvizsglja, hogy a 
    projektek kztt van e olyan, melyben a paramterben kapott nvjegy adatai szerepelnek, viszont annak a 
    projekt listjban nincs benne ez a projekt, ekkor az adott projektbl kitrli a feleslegesen trolt 
    nvjegyet. Ezutn megvizsglja hogy a kapott nvjegy tartalmaz-e projektet az adatai kztt s ha igen, 
    akkor a projekt nvjegy mezjbe is letroljuk a nvjegyet, de csak abban az esetben, ha mg eddig nem volt 
    trolva. */
    modifyItems(contact: Contact): void{
        if(this.projects){
            let projectToBeModified = this.projects
                .filter(x => x.contact.find(cont => cont.id === contact.id))
                .filter(project => !contact.project.includes(project));
            projectToBeModified.forEach(project => {
                project.contact.splice(project.contact.indexOf(contact), 1);
            });
            if(contact.project.length > 0){
                contact.project.forEach((project: Project) => {
                    const actualProject = this.projects.find(proj => proj.id === project.id);
                    if(actualProject.contact.filter(cont => cont.id === contact.id).length === 0){
                        actualProject.contact.push(contact);
                    }
                });
            }
        } else {
            while(this.isLoading === true){
                continue;
            }
            this.modifyItems(contact);
        }
    }

    /* Hasonlan a modifyItems metdushoz, ez is a megfelel felhasznli lmny biztostsa miatt kszlt el.
    Ez a metdus, akkor hajtdik vgre ha a paramterben kapott cget/nvjegyet trlni szeretnnk, de nem 
    szeretnnk hogy a projektek kztt tovbbra is megtalhat legyen a cg/nvjegy. Ekkor ahelyett, hogy
    jra betltennk a friss adatokat az adatbzisbl, annyit tesznk hogy a projektek kzl kikeressk azokat,
    melyekben le van trolva a paramterben kapott rtk s egyszeren kitrljk ezt az rtket bellk. */
    deleteItems(item: Company | Contact): void{
        if(this.projects){
            if(item.hasOwnProperty('taxnumber')){
                this.projects.filter(projects => projects.company.find(company => company.id === item.id))
                .forEach(project => {
                    project.company.splice(project.company.indexOf(item as Company), 1);
                });
            } else if(item.hasOwnProperty('full_name')) {
                this.projects.filter(projects => projects.contact.find(contact => contact.id === item.id))
                .forEach(project => {
                    project.contact.splice(project.contact.indexOf(item as Contact), 1);
                });
            }
        } else {
            while(this.isLoading === true){
                continue;
            }
            this.deleteItems(item);
        }
    }
}


