import { Project } from './../project';
import { ProjectService } from './../project.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-project-list',
  templateUrl: './project-list.component.html',
  styleUrls: ['./project-list.component.scss']
})
export class ProjectListComponent implements OnInit {
  constructor(
    public projectService: ProjectService
  ) {}

  ngOnInit() {
  }

  /*A paramterben kapott project-et vizsgljuk ebben a fggvnyben s ez alapjn adunk vissza egy stringet, 
  melynek rtke egy szn lesz s ezt fogjuk hasznlni az adott projekt htternek a listban. Megnzzk hogy a 
  sttusza 'Ksz'-e? Ha igen, akkor szrkre vltoztatjk a htteret, ha nem akkor tovbb vizsgldunk. 
  Megvizsgljuk, hogy a priorits 'Nagyon fontos', ekkor vilgospiros vagy 'Fontos', ekkor vilgossrga lesz a 
  httere a paramterben kapott rtknek. Ellenkez esetben nem vltoztazunk a httr sznn.*/
  getBackgroundColor(project: Project): string{
    if(project.status && project.status.state === 'Ksz'){
      return '#eee';
    } else if(project.priority) {
      switch (project.priority.value){
        case 'Nagyon fontos':
          return '#ffe6e6';
        case 'Fontos':
          return 'lightgoldenrodyellow';
        default:
          return 'none';
      }
    }
  }

  /*Hasonlan a getBackgroundColor fggvny elejhez itt is megvizsgljuk a kapott projekt sttuszt s ha az rtk 'Ksz', akkor itt a betsnt vltoztatjk szrkre.*/
  getColor(project: Project): string{
    if(project.status && project.status.state === 'Ksz'){
      return '#aaa'
    }
    return 'none';
  }
}



