import { Subscription } from 'rxjs/Subscription';
import { CompanyService } from './../../companies/company.service';
import { Project } from './../project';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ProjectService } from './../project.service';
import { Component, OnInit, ChangeDetectorRef, AfterViewChecked } from '@angular/core';
import { BaseEditComponent } from '../../base/base-edit.component';
import { Company } from '../../companies/company';


const MONEY_REGEX = /^(0|[1-9][0-9]*)$/;

@Component({
  selector: 'app-project-edit',
  templateUrl: './project-edit.component.html',
  styleUrls: ['./project-edit.component.scss']
})
export class ProjectEditComponent extends BaseEditComponent implements OnInit, AfterViewChecked {
	projectForm: FormGroup;
	project: Project;
	companyChanged: boolean = false;
	subscription: Subscription;
	companyArray: Company[];

  constructor(
		public companyService: CompanyService,
		protected route: ActivatedRoute,
		protected router: Router,
		public projectService: ProjectService,
		private fb: FormBuilder,
		private changeDetector: ChangeDetectorRef
	) {
	  super(route, router);
    }

	initform(): void{
		/*Ltrehozzunk egy formgroupot, amiben az input mezk s a rjuk vonatkoz jogosultsgi szablyok tallhatk.*/
		this.projectForm = this.fb.group({
			'projectName': [null, Validators.required],
			'projectIncome': [null, Validators.pattern(MONEY_REGEX)],
			'projectExpenditure': [null, Validators.pattern(MONEY_REGEX)],
			'projectDescription': [],
			'projectChecklist': [],
			'projectFile': [],
			'projectCompany': [],
			'projectStatus': [],
			'projectPriority': [],
			'projectCurrency': []
		});
	}

  ngOnInit(): void{
	this.projectService.getEditItems();
    this.initform();
    if(this.projectService.getItems() && !this.project)
    {
      this.setThis();
    }
  }

	ngAfterViewChecked(): void{
		/*Mivel elbb tlt be a view, mint az adatok ezrt szksgvel az albbi metdusra, hogy ne kapjunk hibazenetet.*/
		if(!this.project){
			this.setThis();
		} else {
			/*A router url-nek paramterei alapjn az array-ben cg id-k lesznek tallhatak, gy a CompanyService segtsgvel kinyerjk a megfelel cgeket s belerakjuk a projekt company mezjbe.*/
			if(this.route.snapshot.params['array']){
				if(this.project.company.length === 0 && this.companyService.getItems()){
					this.route.snapshot.params['array'].split(",").forEach((x: number) =>{
						const company = this.companyService.getItem(+x);
						this.project.company.push(company)});
				}
			}
		}
		this.changeDetector.detectChanges();
	}

  setThis(): void{
		if(this.route.snapshot.routeConfig.path == "new")
		{
			//Ha az url "new"-val egyenl, akkor teljesl
			this.setNew();
		}
		else
		{
			//Itt az url minden esetben "edit/:id" lesz s :id helyn, pedig a cg id-je.
			this.setEdit();
		}
	}

	setNew(): void{
		this.project = new Project();
	}

	setEdit(): void{
    this.project = this.projectService.getItem(+this.route.snapshot.params['id'])
		this.edit = true;	//Ezen mez alapjn tudja a company-edit.component, hogy szerkeszteni kell vagy j cget ltrehozni
	}
	
	/*Amg nem tudja kinyerni a szksges dtumot, addig a mai dtumot adja vissza. Nem a legjobb megolds, de 
	mire betlti az adatokat addigra sikerl is kinyerni a megfelel dtumot, szval a felhasznl nem vesz szre 
	belle semmit*/
	date(){
		if(this.project){
			return new FormControl(this.project.deadline);
		}
		return new FormControl(new Date());
	}

	updateDeadline(event){
		console.log(event);
	}

	//Megvizsgl kt objektet, hogy azonosak-e.
	compareFn(c1: any, c2: any): boolean {
		return c1 && c2 ? c1.id === c2.id : c1 === c2;
	}

  save(): void{
		this.projectService.update(this.project);
		if(this.companyChanged){
			this.companyService.modifyItems(this.project);
		}
    this.navigateBack();
	}

	add(project: Project): void{
		this.projectService.add(project)
		if(project.company.length > 0){
			this.companyService.modifyItems(project);
		}
    	this.navigateBack();
	}

	//Submit lenyomsakor hvdik meg
	onSubmit(project: Project){
		if(this.projectForm.valid)  //Ha a validits megfelel
			this.edit? this.save() : this.add(project);  //Ha az edit true, akkor a save hvdik meg, klnben az add
	}

}



